import './Menu.css';

import { Link } from 'react-router-dom';

export default function Menu({isMenuOpened,setIsMenuOpened}){

    function closeMenu(){
        setIsMenuOpened(false);
    }

    return(
        <nav className={isMenuOpened ? 'menu menu_opened' : 'menu'}>
                <Link to='/about' className="menu__nav-link" onClick={closeMenu}> </Link>
                <Link to='/calendar' className="menu__nav-link" onClick={closeMenu}></Link>
                <Link to='/disciplines' className="menu__nav-link" onClick={closeMenu}></Link>
                <Link to='/news' className="menu__nav-link" onClick={closeMenu}></Link>
                <Link to='/contacts' className="menu__nav-link" onClick={closeMenu}></Link>
        </nav>
    )

}